div.part10c {
    display: flex;
    flex-direction: column;
    align-items: center;
    /* justify-content: center; */
}

.pic {
    max-width: 180px;
    max-height: 180px;
}

h1 {
    font-size: 3em;
}

button {
    border: 1px solid lightgray;
    background-color: transparent;
    padding: 10px;
    border-radius: 4px;   
}

button:hover {
    cursor: pointer;
}

button:focus {
    outline:0;
}

.tracklist {
    text-decoration: none;
    color: #333333;
}

.tracklist:hover {
    color: #00a;
}

.tracklistEntity {
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px gray solid;
    border-radius: 5px;
    padding-left: 10px;
    margin-bottom: 10px;
    background-color: transparent;
    box-shadow: #bbb 2px 2px 5px;
    -webkit-transition: all 0.1s ease-in-out;
    transition: all 0.1s ease-in-out,;
}

.tracklistEntity:hover {
    background-color: #fff;
    box-shadow: #999 2px 2px 5px;
}

.searchInput {
    align-self: center;    
    padding: 12px 20px;
    width: 250px;
    max-width: 500px;
    box-sizing: border-box;
    text-align: center;
    border: 2px solid #ccc;
    border-radius: 4px;
    font-size: 16px;
    background-color: #f8f8f8;
    /* background-image: url(https://www.w3schools.com/css/searchicon.png); */
    background-position: 10px 10px;
    background-repeat: no-repeat;
    padding-left: 42px;
    -webkit-transition: all 0.25s ease-in-out;
    transition: all 0.25s ease-in-out;
}

.searchInput:focus {
    width: 500px;
    border: 3px solid #555;
    outline: none;
  }

form {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}